import React, { useState, useEffect } from 'react';
import { UseUserContext, UpdateUsername, UserThemeProvider } from '../UserContext';
import { useHistory } from 'react-router-dom';
import '../bootstrap/dist/css/bootstrap.css';
import '../App.css';
import * as Bootstrap from 'react-bootstrap';
import axios from 'axios';

function SideComponent(props) {
    const history = useHistory();
    const theme = UpdateUsername();
    const [signOut, setSignOut] = useState('');
    const [userClickedSignOut, setUserCLickedSignOut] = useState(false);

    function HandleSignOut() {
        setUserCLickedSignOut(true);
        setSignOut('a');
        }

    useEffect(() => {
        axios.get('/api/signOut/').then(() => {

            if (userClickedSignOut === true) {
                history.push('/');
            }           


        }).catch(() => {

            if (userClickedSignOut === true) {

                <Bootstrap.Alert variant="primary">
                    <Bootstrap.Alert.Heading>We could't sign you out!!!</Bootstrap.Alert.Heading>
                </Bootstrap.Alert>

            }


        })
    }, [signOut])


    return (
        <>
            <div>
                <div className="SideF-view dark my-3 mx-1  text-left" bg="dark" variant="dark">
                    <svg width="2em" height="2em" viewBox="0 0 16 16" className="bi bi-person-circle" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                        <path d="M13.468 12.37C12.758 11.226 11.195 10 8 10s-4.757 1.225-5.468 2.37A6.987 6.987 0 0 0 8 15a6.987 6.987 0 0 0 5.468-2.63z" />
                        <path fillRule="evenodd" d="M8 9a3 3 0 1 0 0-6 3 3 0 0 0 0 6z" />
                        <path fillRule="evenodd" d="M8 1a7 7 0 1 0 0 14A7 7 0 0 0 8 1zM0 8a8 8 0 1 1 16 0A8 8 0 0 1 0 8z" />
                    </svg>
                </div>
                <p className="Description Text">
                    {props.children}
                </p>
                <div>
                    <Bootstrap.Nav defaultActiveKey="/home" className="flex-column Description">
                        <Bootstrap.Nav.Link href="/home" className="Display-Invisible">Active</Bootstrap.Nav.Link>
                        <Bootstrap.Nav.Link eventKey="link-1" className="Display-Invisible">Features</Bootstrap.Nav.Link>
                        <Bootstrap.Nav.Link eventKey="link-2" className="Display-Invisible">About Us</Bootstrap.Nav.Link>
                        <Bootstrap.Nav.Link eventKey="disabled" disabled className="Display-Invisible">
                            Disabled
                        </Bootstrap.Nav.Link>
                        <Bootstrap.Nav.Link eventKey="disabled" onClick={HandleSignOut}>
                            Log Out
                        </Bootstrap.Nav.Link>
                    </Bootstrap.Nav>
                </div>

            </div>
        </>
    )
}

export default SideComponent;