import React, { useState, useEffect } from 'react';
import LoggedNavBar from './LoggedNavBar';
import * as Bootstrap from 'react-bootstrap';
import '../bootstrap/dist/css/bootstrap.css';
import '../App.css';
import SideComponent from './SideComponent';
import axios from 'axios';
import LoadingComponent from './LoadingComponent';
import REACTDOM from 'react-dom';
import Footer from '../Home_Components/Footer';

function LoggedHome(props) {
    const [hasSavedNotes, setHasSavedNotes] = useState(false);
    const [isLoading, setIsLoading] = useState(true);
    const [data, setData] = useState([]);

    const username = props.location.username;

    useEffect(() => {
        axios.get('/api/getNotes/' + username).then(res => {   
            setData(res.data);
            if (data.length === undefined || res.data.length === undefined) {
                setIsLoading(false);
                setHasSavedNotes(false);
            }
            else {
                console.log("This is the length of the array", res.data.length)
                setIsLoading(false);
                setHasSavedNotes(true);
                /*setData(res.data);*/
                console.log("Data might be set into varaible or constant");
                console.log(data);
            }

        }).catch(err => {
            console.log("This is the error", err);
            /*setIsLoading(false);*/
            <Bootstrap.Alert variant="primary">
                <Bootstrap.Alert.Heading>We could't get your password savers</Bootstrap.Alert.Heading>
            </Bootstrap.Alert>
        })
    }, [])


    return (
        <>
            <div>
                <LoggedNavBar>
                    {username}
                </LoggedNavBar>
            </div>
            <div className="Container_page">
                <div className="Left_page bg-dark">
                    <SideComponent>
                        {username}
                    </SideComponent>
                </div>
                <div className="Right_page">
                    <Bootstrap.Nav justify variant="tabs" defaultActiveKey="/home">
                        <Bootstrap.Nav.Item>
                            <Bootstrap.Nav.Link href="#">Notes</Bootstrap.Nav.Link>
                        </Bootstrap.Nav.Item>
                    </Bootstrap.Nav>

                    <div>
                        {
                            isLoading === true ?

                                <div className="ml-auto" style={{ marginTop: "150px" }}>
                                    <LoadingComponent />
                                </div>

                                :
                                <div>

                                    {
                                        hasSavedNotes === false &&
                                        <p className="text-muted">
                                            You do not have any saved sites....
                                    </p>
                                    }



                                    {hasSavedNotes === true &&
                                        <div>
                                            {
                                                data.map(fr => {
                                                    return <Bootstrap.Accordion>
                                                        <Bootstrap.Card>
                                                            <Bootstrap.Card.Header>
                                                                <Bootstrap.Accordion.Toggle as={Bootstrap.Card.Header} variant="link" eventKey="0">
                                                                    Website name : {fr?.url}
                                                                </Bootstrap.Accordion.Toggle>
                                                            </Bootstrap.Card.Header>
                                                            <Bootstrap.Accordion.Collapse eventKey="0">
                                                                <Bootstrap.Card.Body>
                                                                    <table className="mx-auto">
                                                                        <tr>
                                                                            <td>Username : {fr?.username}</td>
                                                                        </tr>
                                                                        <tr>
                                                                            <td>Password : {fr?.password}</td>
                                                                        </tr>
                                                                    </table>
                                                                </Bootstrap.Card.Body>
                                                            </Bootstrap.Accordion.Collapse>
                                                        </Bootstrap.Card>
                                                    </Bootstrap.Accordion>
                                                })}

                                        </div>
                                    }
                                </div>
                        }
                    </div>
                </div>



            </div>
            <div>
                        <Footer />
                
            </div>

        </>
    )
}


export default LoggedHome;



